<template lang="pug">
v-container.sign-in.fill-height.d-flex.flex-column.justify-center.align-center
  v-card(width="500")
    v-form(@submit.prevent="signIn", v-model="isValid")
      v-card-title Sign in
      v-card-text
        v-text-field(
          autofocus
          label="Email",
          type="email",
          required="",
          v-model="form.email",
          :rules="emailRules"
        )
        v-text-field(
          label="Password",
          type="password",
          required="",
          v-model="form.password",
          :rules="passwordRules"
        )
      v-card-actions
        v-btn(text :to="{name: 'resetPassword', params: {email: form.email}}") Forgot password?
        v-spacer
        v-btn(text, color="primary", type="submit", :disabled="!isValid") Sign in
    v-fade-transition
      v-overlay(absolute, opacity="0.2", v-if="loading")
        v-progress-circular(indeterminate)
</template>

<script>
import { emailRules, passwordRules } from '@/plugins/inputValidation'

export default {
  name: "signIn",
  metaInfo: {
    title: 'Sign in',
  },
  data: () => ({
    form: {
      email: "",
      password: "",
    },
    isValid: false,
    emailRules,
    passwordRules,
    loading: false,
  }),
  methods: {
    async signIn() {
      this.loading = true;
      try {
        await this.$store.dispatch("signIn", this.form);
      }
      finally {
        this.loading = false;
      }
    },
  },
};
</script>
